.AboutMe {
    padding: 20px 0;
    background-color: #f9f9f9;
  }
  
  .AboutMeContent {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .AboutMeImage {
    flex: 1;
    max-width: 300px;
    margin-right: 40px;
  }
  
  .AboutMeImage img {
    display: block;
    width: 100%;
    border-radius: 50%;
  }
  
  .AboutMeText {
    flex: 1;
  }
  
  .AboutMeText h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .AboutMeText p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #666;
  }
  