.Navbar {
  
  top: 100%;
  left: 0;
  width: 100%;
  background-color: transparent;
}

.NavbarContainer {
  display: flex;
  justify-content: center;
}

.NavbarList {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.NavbarItem {
  margin-right: 20px;
}

.NavbarItem:last-child {
  margin-right: 0;
}

.Link {
  cursor: pointer;
}

.Link.active {
  font-weight: bold;
}

nav {
  height: 50px;
}