.ProjectIcons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .ProjectIcon {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }
  
  .ProjectIcon img {
    width: 80px;
    height: 80px;
  }
  
  .ProjectIconTitle {
    margin-top: 10px;
    font-size: 1.2rem;
    text-align: center;
    color: #666;
  }
  