.ContactForm {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .ContactForm h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .ContactForm input,
  .ContactForm textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .ContactForm textarea {
    height: 120px;
  }
  
  .ContactForm p {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .SuccessMessage {
    color: green;
  }
  
  .ErrorMessage {
    color: red;
  }
  
  .ContactForm button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #333;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .ContactForm button:hover {
    background-color: #555;
  }
  