.Experience {
    padding: 80px 0;
    background-color: #f9f9f9;
  }
  
  .ExperienceContent {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .Experience h2 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 40px;
  }
  
  .ExperienceList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .ExperienceItem {
    width: 100%;
    max-width: 400px;
    margin-bottom: 40px;
  }
  
  .ExperienceTitle {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }
  
  .ExperienceTime {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 10px;
  }
  
  .ExperienceDescription {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #666;
  }
  