.Hero {
    position: relative;
    height: 80vh;
    background-image: url('./hero.jpg');
    background-size: cover;
    background-position: center;
    overflow: hidden;
  }
  
  .HeroOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Adjust the opacity as needed */
    backdrop-filter: blur(2px); /* Apply the desired blur effect */
  }
  
  .HeroContent {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    z-index: 1;
  }
  
  .HeroTitle {
    font-size: 3rem;
    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Add shadow effect to the title */
  }
  